import { simulateDeutanPoly, correctForDeutan, type RGB8 } from './deutan-simulator';
import type { ColorVisionSettings } from '../types/color';

/**
 * ç”»åƒã«è‰²å¤‰æ›ã‚’é©ç”¨
 * @param imageData Canvas ImageData
 * @param colorVisionSettings è‰²è¦šè¨­å®š
 * @param onProgress é€²æ—ã‚³ãƒ¼ãƒ«ãƒãƒƒã‚¯
 * @returns å¤‰æ›ã•ã‚ŒãŸImageData
 */
export async function processImageData(
  imageData: ImageData,
  colorVisionSettings: ColorVisionSettings,
  onProgress?: (progress: number) => void
): Promise<ImageData> {
  console.log('processImageData called with settings:', colorVisionSettings);
  console.log('Image data size:', imageData.width, 'x', imageData.height);
  
  const data = new Uint8ClampedArray(imageData.data);
  const totalPixels = data.length / 4;
  const batchSize = Math.max(1000, Math.floor(totalPixels / 100)); // ãƒãƒƒãƒã‚µã‚¤ã‚ºèª¿æ•´
  
  let processedPixels = 0;
  let actuallyProcessed = false;

  // ãƒãƒƒãƒå‡¦ç†ã§ãƒ¡ã‚¤ãƒ³ã‚¹ãƒ¬ãƒƒãƒ‰ã‚’ãƒ–ãƒ­ãƒƒã‚¯ã—ãªã„ã‚ˆã†ã«
  for (let i = 0; i < data.length; i += batchSize * 4) {
    const endIndex = Math.min(i + batchSize * 4, data.length);
    
    // ãƒãƒƒãƒå†…ã®ãƒ”ã‚¯ã‚»ãƒ«ã‚’å‡¦ç†
    for (let j = i; j < endIndex; j += 4) {
      const originalRgb: RGB8 = [data[j], data[j + 1], data[j + 2]];
      let transformedRgb = originalRgb;
      
      // ã‚·ãƒŸãƒ¥ãƒ¬ãƒ¼ã‚·ãƒ§ãƒ³ã‚’é©ç”¨
      if (colorVisionSettings.simulation.useSimulation && 
          colorVisionSettings.simulation.simulationType === 'deuteranopia') {
        transformedRgb = simulateDeutanPoly(
          transformedRgb, 
          colorVisionSettings.simulation.severity
        );
        actuallyProcessed = true;
      }
      
      // è£œæ­£ã‚’é©ç”¨
      if (colorVisionSettings.correction.useCorrection && 
          colorVisionSettings.correction.correctionType === 'deuteranopia') {
        transformedRgb = correctForDeutan(
          transformedRgb, 
          colorVisionSettings.correction.strength
        );
        actuallyProcessed = true;
      }
      
      // çµæœã‚’æ›¸ãæˆ»ã—
      data[j] = transformedRgb[0];
      data[j + 1] = transformedRgb[1];
      data[j + 2] = transformedRgb[2];
      // alpha channel (data[j + 3]) ã¯ãã®ã¾ã¾
      
      processedPixels++;
    }
    
    // é€²æ—ã‚’å ±å‘Š
    if (onProgress) {
      const progress = Math.round((processedPixels / totalPixels) * 100);
      onProgress(progress);
    }
    
    // ãƒ¡ã‚¤ãƒ³ã‚¹ãƒ¬ãƒƒãƒ‰ã«åˆ¶å¾¡ã‚’æˆ»ã™
    await new Promise(resolve => setTimeout(resolve, 0));
  }
  
  console.log('Processing completed. Actually processed:', actuallyProcessed);
  console.log('Total pixels processed:', processedPixels);
  
  return new ImageData(data, imageData.width, imageData.height);
}

/**
 * ç”»åƒãƒ•ã‚¡ã‚¤ãƒ«ã‚’Canvasã«èª­ã¿è¾¼ã¿
 * @param file ç”»åƒãƒ•ã‚¡ã‚¤ãƒ«
 * @param maxWidth æœ€å¤§å¹…ï¼ˆãƒªã‚µã‚¤ã‚ºç”¨ï¼‰
 * @param maxHeight æœ€å¤§é«˜ã•ï¼ˆãƒªã‚µã‚¤ã‚ºç”¨ï¼‰
 * @returns Canvasè¦ç´ ã¨ImageData
 */
export function loadImageToCanvas(
  file: File, 
  maxWidth = 4096, 
  maxHeight = 4096
): Promise<{canvas: HTMLCanvasElement, imageData: ImageData}> {
  return new Promise((resolve, reject) => {
    const img = new Image();
    const canvas = document.createElement('canvas');
    const ctx = canvas.getContext('2d');
    
    if (!ctx) {
      reject(new Error('Canvas context not available'));
      return;
    }
    
    img.onload = () => {
      // ãƒªã‚µã‚¤ã‚ºè¨ˆç®—
      let { width, height } = img;
      if (width > maxWidth || height > maxHeight) {
        const ratio = Math.min(maxWidth / width, maxHeight / height);
        width *= ratio;
        height *= ratio;
      }
      
      canvas.width = width;
      canvas.height = height;
      
      // ç”»åƒã‚’æç”»
      ctx.drawImage(img, 0, 0, width, height);
      
      // ImageDataã‚’å–å¾—
      const imageData = ctx.getImageData(0, 0, width, height);
      
      resolve({ canvas, imageData });
    };
    
    img.onerror = () => {
      reject(new Error('Failed to load image'));
    };
    
    img.src = URL.createObjectURL(file);
  });
}

/**
 * ImageDataã‚’Canvasã«æç”»
 * @param canvas Canvasè¦ç´ 
 * @param imageData ImageData
 */
export function drawImageDataToCanvas(
  canvas: HTMLCanvasElement, 
  imageData: ImageData
): void {
  const ctx = canvas.getContext('2d');
  if (!ctx) throw new Error('Canvas context not available');
  
  canvas.width = imageData.width;
  canvas.height = imageData.height;
  ctx.putImageData(imageData, 0, 0);
}

/**
 * Canvasã‚’Blob/Fileå½¢å¼ã§ãƒ€ã‚¦ãƒ³ãƒ­ãƒ¼ãƒ‰
 * @param canvas Canvasè¦ç´ 
 * @param filename ãƒ•ã‚¡ã‚¤ãƒ«å
 * @param format ç”»åƒãƒ•ã‚©ãƒ¼ãƒãƒƒãƒˆ
 * @param quality å“è³ªï¼ˆJPEGç”¨ï¼‰
 */
export function downloadCanvasAsImage(
  canvas: HTMLCanvasElement,
  filename = 'colorwise-processed.png',
  format: 'image/png' | 'image/jpeg' | 'image/webp' = 'image/png',
  quality = 0.9
): void {
  canvas.toBlob((blob) => {
    if (!blob) return;
    
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = filename;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  }, format, quality);
}

/**
 * ãƒ•ã‚¡ã‚¤ãƒ«ã‚µã‚¤ã‚ºã‚’äººé–“ã«èª­ã¿ã‚„ã™ã„å½¢å¼ã«å¤‰æ›
 * @param bytes ãƒã‚¤ãƒˆæ•°
 * @returns ãƒ•ã‚©ãƒ¼ãƒãƒƒãƒˆã•ã‚ŒãŸæ–‡å­—åˆ—
 */
export function formatFileSize(bytes: number): string {
  if (bytes === 0) return '0 Bytes';
  
  const k = 1024;
  const sizes = ['Bytes', 'KB', 'MB', 'GB'];
  const i = Math.floor(Math.log(bytes) / Math.log(k));
  
  return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
}

/**
 * ç”»åƒãƒ•ã‚¡ã‚¤ãƒ«ã®å¦¥å½“æ€§ã‚’ãƒã‚§ãƒƒã‚¯
 * @param file ãƒ•ã‚¡ã‚¤ãƒ«
 * @param maxSize æœ€å¤§ã‚µã‚¤ã‚ºï¼ˆãƒã‚¤ãƒˆï¼‰
 * @returns ã‚¨ãƒ©ãƒ¼ãƒ¡ãƒƒã‚»ãƒ¼ã‚¸ã¾ãŸã¯null
 */
export function validateImageFile(
  file: File, 
  maxSize = 10 * 1024 * 1024 // 10MB
): string | null {
  // ãƒ•ã‚¡ã‚¤ãƒ«ã‚¿ã‚¤ãƒ—ãƒã‚§ãƒƒã‚¯
  if (!file.type.startsWith('image/')) {
    return 'ç”»åƒãƒ•ã‚¡ã‚¤ãƒ«ã‚’é¸æŠã—ã¦ãã ã•ã„';
  }
  
  // ã‚µãƒãƒ¼ãƒˆã•ã‚Œã¦ã„ã‚‹å½¢å¼ãƒã‚§ãƒƒã‚¯
  const supportedTypes = ['image/jpeg', 'image/png', 'image/webp', 'image/gif'];
  if (!supportedTypes.includes(file.type)) {
    return 'ã‚µãƒãƒ¼ãƒˆã•ã‚Œã¦ã„ãªã„ç”»åƒå½¢å¼ã§ã™ï¼ˆJPEG, PNG, WebP, GIFã®ã¿ï¼‰';
  }
  
  // ãƒ•ã‚¡ã‚¤ãƒ«ã‚µã‚¤ã‚ºãƒã‚§ãƒƒã‚¯
  if (file.size > maxSize) {
    return `ãƒ•ã‚¡ã‚¤ãƒ«ã‚µã‚¤ã‚ºãŒå¤§ãã™ãã¾ã™ï¼ˆæœ€å¤§: ${formatFileSize(maxSize)}ï¼‰`;
  }
  
  return null;
}