        setOriginalImageData(imageData);
        
        // ã‚ªãƒªã‚¸ãƒŠãƒ«ç”»åƒã‚’canvasã«æç”»
        if (originalCanvasRef.current) {
          drawImageDataToCanvas(originalCanvasRef.current, imageData);
        }
        
        // ç”»åƒèª­ã¿è¾¼ã¿å®Œäº†æ™‚ã¯ processingçŠ¶æ…‹ã‚’ç¶™ç¶šï¼ˆè‰²è¦šå‡¦ç†å¾…ã¡ï¼‰
        console.log('Image loaded, waiting for color processing...');
