// deutan_poly_full.ts
// Machado 2009 Deuteranomaly matrices (full 0.0..1.0 step 0.1) â†’ polynomialized (degree=4).
// Provides: simulateDeutanPoly(), daltonizeDeutan().
// Source for matrices: UFRGS page, Table 1 (Deuteranomaly column).

// ---------- sRGB <-> linear ----------
function srgbToLinear(c: number): number {
  return (c <= 0.04045) ? c / 12.92 : Math.pow((c + 0.055) / 1.055, 2.4);
}
function linearToSrgb(c: number): number {
  return (c <= 0.0031308) ? 12.92 * c : 1.055 * Math.pow(c, 1/2.4) - 0.055;
}

// ---------- Linear algebra helpers ----------
type Vec3 = [number, number, number];
type Mat3 = [Vec3, Vec3, Vec3];

function applyMat3(m: Mat3, v: Vec3): Vec3 {
  const [x,y,z] = v;
  return [
    m[0][0]*x + m[0][1]*y + m[0][2]*z,
    m[1][0]*x + m[1][1]*y + m[1][2]*z,
    m[2][0]*x + m[2][1]*y + m[2][2]*z,
  ];
}

function clamp01(x: number): number {
  return Math.max(0, Math.min(1, x));
}

function clamp255(x: number): number {
  return Math.max(0, Math.min(255, Math.round(x)));
}

// ---------- Polynomial fit (least squares) ----------
// Fit y â‰ˆ Î£_{k=0..d} c_k x^k
function polyfitLeastSquares(xs: number[], ys: number[], degree: number): number[] {
  if (xs.length !== ys.length) throw new Error("xs and ys must have the same length");
  const n = xs.length;
  const d = degree;
  if (n < d + 1) throw new Error(`Need at least degree+1 points (got ${n}, degree ${d})`);

  // Build Vandermonde matrix A and vector b
  const A: number[][] = Array.from({length: n}, () => Array(d+1).fill(0));
  const b: number[] = ys.slice();

  for (let i = 0; i < n; i++) {
    let val = 1;
    for (let j = 0; j <= d; j++) {
      A[i][j] = val;
      val *= xs[i];
    }
  }

  // Solve normal equations (A^T A) c = A^T b via Gaussian elimination
  const AT = transpose(A);
  const ATA = matMul(AT, A);
  const ATb = matVecMul(AT, b);

  // Solve ATA * c = ATb
  const c = gaussianSolve(ATA, ATb);
  return c;
}

function transpose(M: number[][]): number[][] {
  const rows = M.length, cols = M[0].length;
  const T = Array.from({length: cols}, () => Array(rows).fill(0));
  for (let i = 0; i < rows; i++) for (let j = 0; j < cols; j++) T[j][i] = M[i][j];
  return T;
}

function matMul(A: number[][], B: number[][]): number[][] {
  const r = A.length, k = A[0].length, c = B[0].length;
  const out = Array.from({length: r}, () => Array(c).fill(0));
  for (let i = 0; i < r; i++) {
    for (let j = 0; j < c; j++) {
      let sum = 0;
      for (let t = 0; t < k; t++) sum += A[i][t] * B[t][j];
      out[i][j] = sum;
    }
  }
  return out;
}

function matVecMul(A: number[][], v: number[]): number[] {
  const r = A.length, c = A[0].length;
  const out = Array(r).fill(0);
  for (let i = 0; i < r; i++) {
    let sum = 0;
    for (let j = 0; j < c; j++) sum += A[i][j] * v[j];
    out[i] = sum;
  }
  return out;
}

function gaussianSolve(Ain: number[][], bIn: number[]): number[] {
  // Augment
  const n = Ain.length;
  const A = Ain.map((row, i) => row.concat([bIn[i]]));

  // Forward elimination
  for (let i = 0; i < n; i++) {
    // Pivot
    let maxRow = i;
    for (let r = i+1; r < n; r++) if (Math.abs(A[r][i]) > Math.abs(A[maxRow][i])) maxRow = r;
    [A[i], A[maxRow]] = [A[maxRow], A[i]];

    const pivot = A[i][i];
    if (Math.abs(pivot) < 1e-12) throw new Error("Singular matrix in gaussianSolve");

    // Normalize row
    for (let j = i; j <= n; j++) A[i][j] /= pivot;

    // Eliminate
    for (let r = 0; r < n; r++) {
      if (r === i) continue;
      const factor = A[r][i];
      for (let j = i; j <= n; j++) A[r][j] -= factor * A[i][j];
    }
  }

  // Extract solution
  const x = A.map(row => row[n]);
  return x;
}

