// ---------- Build polynomial coefficients per element ----------
type CoeffMat = number[][][]; // 3x3 of coefficient arrays [c0..cd]
function buildPolynomialCoeffs(data: SamplePoint[], degree: number): CoeffMat {
  if (degree < 1) throw new Error("degree must be >= 1");
  const xs = data.map(p => p.s);
  const coeffs: CoeffMat = [[],[],[]] as unknown as CoeffMat;

  for (let i = 0; i < 3; i++) {
    coeffs[i] = [];
    for (let j = 0; j < 3; j++) {
      const ys = data.map(p => p.M[i][j]);
      const c = polyfitLeastSquares(xs, ys, degree);
      coeffs[i][j] = c; // length degree+1
    }
  }
  return coeffs;
}

function evalPoly(c: number[], x: number): number {
  // Horner
  let acc = 0;
  for (let k = c.length - 1; k >= 0; k--) acc = acc * x + c[k];
  return acc;
}

function evaluateMatrix(coeffs: CoeffMat, s: number): Mat3 {
  const ss = clamp01(s);
  const M: Mat3 = [[0,0,0],[0,0,0],[0,0,0]] as Mat3;
  for (let i = 0; i < 3; i++) {
    for (let j = 0; j < 3; j++) {
      M[i][j] = evalPoly(coeffs[i][j], ss);
    }
  }
  return M;
}

// Precompute coefficients
const DEGREE = 4;
const COEFFS: CoeffMat = buildPolynomialCoeffs(MACHADO_DEUTERANOMALY, DEGREE);

// ---------- Public API ----------
export type RGB8 = [number, number, number];

/** Simulate Deuteranomaly using polynomialized Machado matrix. */
export function simulateDeutanPoly(rgb: RGB8, severity: number): RGB8 {
  const lin: Vec3 = [
    srgbToLinear(rgb[0]/255),
    srgbToLinear(rgb[1]/255),
    srgbToLinear(rgb[2]/255),
  ];
  const M = evaluateMatrix(COEFFS, severity);
  const [R,G,B] = applyMat3(M, lin);
  return [
    clamp255(linearToSrgb(clamp01(R))*255),
    clamp255(linearToSrgb(clamp01(G))*255),
    clamp255(linearToSrgb(clamp01(B))*255),
  ];
}

/**
 * Daltonize for Deuteranomaly via iterative pre-compensation.
 * Finds X such that S(X) â‰ˆ Y (Y=original). Performs T steps:
 *   X_{k+1} = clip( X_k + alpha * ( Y - S(X_k) ) )
 * All operations in linear RGB; final result returned in sRGB 8-bit.
 */
export function daltonizeDeutan(rgb: RGB8, severity: number, alpha=0.6, steps=3): RGB8 {
  // to linear
  let X: Vec3 = [
    srgbToLinear(rgb[0]/255),
    srgbToLinear(rgb[1]/255),
    srgbToLinear(rgb[2]/255),
  ];
  const Y: Vec3 = [...X];
  const M = evaluateMatrix(COEFFS, severity);

  const simulateLin = (v: Vec3): Vec3 => applyMat3(M, v);

  for (let t = 0; t < steps; t++) {
    const Sx = simulateLin(X);
    // residual towards target Y
    const err: Vec3 = [Y[0]-Sx[0], Y[1]-Sx[1], Y[2]-Sx[2]];
    X = [
      clamp01(X[0] + alpha * err[0]),
      clamp01(X[1] + alpha * err[1]),
      clamp01(X[2] + alpha * err[2]),
    ];
  }

  return [
    clamp255(linearToSrgb(X[0])*255),
    clamp255(linearToSrgb(X[1])*255),
    clamp255(linearToSrgb(X[2])*255),
  ];
}

/**
 * Green enhancement "inverse-ish" filter in linear space.
 * This is not a true inverse; it boosts green while attenuating red/blue.
 * @param rgb 8-bit sRGB triple [0..255]
 * @param strength Î± in [0,1], typical 0.1~0.3
 */
export function enhanceGreen(rgb: RGB8, strength: number = 0.2): RGB8 {
  const s = Math.max(0, Math.min(1, strength));
  const E: Mat3 = [
    [1 - s, 0,     0    ],
    [0,     1 + s, 0    ],
    [0,     0,     1 - s],
  ];
  const [r,g,b] = rgb.map(v => srgbToLinear(v/255)) as Vec3;
  const [R,G,B] = applyMat3(E, [r,g,b]);
  return [
    clamp255(linearToSrgb(R)*255),
    clamp255(linearToSrgb(G)*255),
    clamp255(linearToSrgb(B)*255),
  ];
}

/**
 * ãƒ‡ãƒ¥ãƒ¼ã‚¿ãƒ³è£œæ­£ãƒ•ã‚£ãƒ«ã‚¿ãƒ¼ï¼ˆDaltonizationä½¿ç”¨ï¼‰
 * Machado 2009ãƒ¢ãƒ‡ãƒ«ã«ã‚ˆã‚‹ç§‘å­¦çš„è£œæ­£
 * @param rgb 8-bit sRGB triple [0..255]
 * @param strength è£œæ­£å¼·åº¦ Î± in [0,1], typical 0.1~0.3
 * @returns è£œæ­£ã•ã‚ŒãŸRGBå€¤
 */
export function correctForDeutan(rgb: RGB8, strength: number = 0.2): RGB8 {
  return daltonizeDeutan(rgb, strength, 0.6, 3);
}

/**
 * RGBå€¤é…åˆ—ã«å¯¾ã™ã‚‹ãƒãƒƒãƒå‡¦ç†ç‰ˆ
 * @param rgbArray RGBå€¤ã®é…åˆ—
 * @param severity ãƒ‡ãƒ¥ãƒ¼ã‚¿ãƒ³ã‚·ãƒŸãƒ¥ãƒ¬ãƒ¼ã‚·ãƒ§ãƒ³å¼·åº¦
 * @returns ã‚·ãƒŸãƒ¥ãƒ¬ãƒ¼ã‚·ãƒ§ãƒ³çµæœã®é…åˆ—
 */
export function simulateDeutanBatch(rgbArray: RGB8[], severity: number): RGB8[] {
  return rgbArray.map(rgb => simulateDeutanPoly(rgb, severity));
}

/**
 * Canvas ImageDataã«å¯¾ã™ã‚‹ãƒ‡ãƒ¥ãƒ¼ã‚¿ãƒ³ã‚·ãƒŸãƒ¥ãƒ¬ãƒ¼ã‚·ãƒ§ãƒ³
 * @param imageData Canvas ImageData
 * @param severity ã‚·ãƒŸãƒ¥ãƒ¬ãƒ¼ã‚·ãƒ§ãƒ³å¼·åº¦
 * @returns å¤‰æ›ã•ã‚ŒãŸImageData
 */
export function simulateDeutanImageData(imageData: ImageData, severity: number): ImageData {
  const data = new Uint8ClampedArray(imageData.data);
  
  for (let i = 0; i < data.length; i += 4) {
    const [r, g, b] = simulateDeutanPoly([data[i], data[i + 1], data[i + 2]], severity);
    data[i] = r;
    data[i + 1] = g;
    data[i + 2] = b;
    // alpha channel (data[i + 3]) is preserved
  }
  
  return new ImageData(data, imageData.width, imageData.height);
}
